'use client'

import { PublicKey } from '@solana/web3.js'
import { useMemo, useState, useEffect } from 'react'
import { useCoinFlipProgram, useCoinFlipProgramAccount } from './coin-flip-data-access'
import { Button } from '@/components/ui/button'
import { useWallet } from '@solana/wallet-adapter-react'
import { Coins, Trophy, TrendingUp, History } from 'lucide-react'
import CoinFlipAnimation from './CoinFlipAnimation'
import BN from 'bn.js'
import { useQuery } from '@tanstack/react-query'
import { useConnection } from '@solana/wallet-adapter-react'

const LAMPORTS_PER_SOL = 1_000_000_000

// Helper function to format wallet address
function formatWalletAddress(address: string, chars = 4) {
  return `${address.slice(0, chars)}...${address.slice(-chars)}`
}

// Component to fetch and display top winners
function TopWinnersPanel() {
  const { program } = useCoinFlipProgram()
  
  const topWinners = useQuery({
    queryKey: ['top-winners-recent'],
    queryFn: async () => {
      if (!program) return []
      
      try {
        const betAccounts = await (program.account as any).betAccount.all()
        const winningBets = betAccounts
          .filter((bet: any) => bet.account.settled && bet.account.won)
          .map((bet: any) => ({
            user: bet.account.user.toString(),
            amount: bet.account.amount.toNumber() / LAMPORTS_PER_SOL,
            gameNumber: bet.account.gameNumber?.toNumber() || 0
          }))
        
        const recentWinningBets = winningBets
          .sort((a: { gameNumber: number }, b: { gameNumber: number }) => b.gameNumber - a.gameNumber)
          .slice(0, 20)
        
        const userWinnings = recentWinningBets.reduce((acc: any, bet: any) => {
          const userKey = bet.user
          if (!acc[userKey]) {
            acc[userKey] = {
              user: userKey,
              totalWinnings: 0,
              gamesWon: 0,
              lastGameNumber: 0
            }
          }
          acc[userKey].totalWinnings += bet.amount
          acc[userKey].gamesWon += 1
          acc[userKey].lastGameNumber = Math.max(acc[userKey].lastGameNumber, bet.gameNumber)
          return acc
        }, {})
        
        return Object.values(userWinnings)
          .sort((a: any, b: any) => {
            if (b.totalWinnings !== a.totalWinnings) {
              return b.totalWinnings - a.totalWinnings
            }
            return b.lastGameNumber - a.lastGameNumber
          })
          .slice(0, 4)
          .map((winner: any, index: number) => ({
            rank: index + 1,
            player: formatWalletAddress(winner.user),
            winnings: winner.totalWinnings,
            gamesWon: winner.gamesWon
          }))
      } catch (error) {
        console.error('Error fetching top winners:', error)
        return []
      }
    },
    enabled: !!program,
    refetchInterval: 30000,
  })

  return (
    <div className="bg-white/80 backdrop-blur-sm rounded-xl p-6 shadow-lg border border-white/20">
      <h3 className="text-lg font-bold mb-4 flex items-center gap-2 text-gray-800">
        <Trophy className="w-5 h-5 text-yellow-500" />
        Top Winners
      </h3>
      
      {topWinners.isLoading ? (
        <div className="space-y-3">
          {[...Array(4)].map((_, i) => (
            <div key={i} className="animate-pulse flex items-center justify-between">
              <div className="flex items-center gap-3">
                <div className="w-6 h-6 bg-gray-300 rounded-full"></div>
                <div className="h-4 bg-gray-300 rounded w-20"></div>
              </div>
              <div className="h-4 bg-gray-300 rounded w-16"></div>
            </div>
          ))}
        </div>
      ) : !topWinners.data || topWinners.data.length === 0 ? (
        <div className="text-center py-4 text-gray-500">
          <Trophy className="w-8 h-8 mx-auto mb-2 text-gray-400" />
          <p className="text-sm">No recent winners</p>
        </div>
      ) : (
        <div className="space-y-3">
          {topWinners.data.map((winner: any) => (
            <div key={winner.rank} className="flex items-center justify-between">
              <div className="flex items-center gap-3">
                <div className={`w-6 h-6 rounded-full flex items-center justify-center text-xs font-bold text-white ${
                  winner.rank === 1 ? 'bg-yellow-500' : 
                  winner.rank === 2 ? 'bg-gray-400' : 
                  winner.rank === 3 ? 'bg-orange-500' : 'bg-gray-600'
                }`}>
                  {winner.rank}
                </div>
                <div>
                  <span className="text-sm font-medium text-gray-900">{winner.player}</span>
                  <div className="text-xs text-gray-500">{winner.gamesWon} wins</div>
                </div>
              </div>
              <span className="text-sm font-semibold text-green-600">
                +{winner.winnings.toFixed(2)} SOL
              </span>
            </div>
          ))}
        </div>
      )}
    </div>
  )
}

// Component to fetch and display recent activity
function RecentActivityPanel() {
  const { program } = useCoinFlipProgram()
  
  const recentBets = useQuery({
    queryKey: ['recent-bets'],
    queryFn: async () => {
      if (!program) return []
      
      try {
        const betAccounts = await (program.account as any).betAccount.all()
        return betAccounts
          .filter((bet: any) => bet.account.settled)
          .sort((a: any, b: any) => (b.account.slot || 0) - (a.account.slot || 0))
          .slice(0, 8)
          .map((bet: any) => ({
            id: bet.publicKey.toString(),
            player: formatWalletAddress(bet.account.user.toString()),
            amount: bet.account.amount.toNumber() / LAMPORTS_PER_SOL,
            choice: bet.account.choice === 0 ? 'Heads' : 'Tails',
            won: bet.account.won,
          }))
      } catch (error) {
        console.error('Error fetching recent bets:', error)
        return []
      }
    },
    enabled: !!program,
    refetchInterval: 30000,
  })

  return (
    <div className="bg-white/80 backdrop-blur-sm rounded-xl p-6 shadow-lg border border-white/20">
      <h3 className="text-lg font-bold mb-4 flex items-center gap-2 text-gray-800">
        <History className="w-5 h-5 text-green-500" />
        Recent Activity
      </h3>
      
      {recentBets.isLoading ? (
        <div className="space-y-3">
          {[...Array(4)].map((_, i) => (
            <div key={i} className="animate-pulse flex items-center justify-between py-2">
              <div className="flex items-center gap-2">
                <div className="w-2 h-2 bg-gray-300 rounded-full"></div>
                <div className="h-4 bg-gray-300 rounded w-20"></div>
              </div>
              <div className="h-4 bg-gray-300 rounded w-12"></div>
            </div>
          ))}
        </div>
      ) : !recentBets.data || recentBets.data.length === 0 ? (
        <div className="text-center py-4 text-gray-500">
          <History className="w-8 h-8 mx-auto mb-2 text-gray-400" />
          <p className="text-sm">No recent activity</p>
        </div>
      ) : (
        <div className="space-y-3 max-h-80 overflow-y-auto">
          {recentBets.data.map((game: any) => (
            <div key={game.id} className="flex items-center justify-between py-2 border-b border-gray-100 last:border-b-0">
              <div className="flex items-center gap-2">
                <div className={`w-2 h-2 rounded-full ${game.won ? 'bg-green-400' : 'bg-red-400'}`}></div>
                <div>
                  <p className="text-sm font-medium text-gray-900">{game.player}</p>
                  <p className="text-xs text-gray-500">{game.choice}</p>
                </div>
              </div>
              <div className="text-right">
                <p className={`text-sm font-semibold ${game.won ? 'text-green-600' : 'text-red-600'}`}>
                  {game.won ? '+' : '-'}{game.amount.toFixed(2)} SOL
                </p>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  )
}

function BettingInterface({ gameStatePubkey }: { gameStatePubkey: PublicKey }) {
  const wallet = useWallet()
  const { accounts, program } = useCoinFlipProgram()
  
  const [betChoice, setBetChoice] = useState<number>(0)
  const [placeBetAmount, setPlaceBetAmount] = useState<number>(0.1)
  const [betResult, setBetResult] = useState<null | 'won' | 'lost' | 'pending'>(null)
  const [showAnimation, setShowAnimation] = useState(false)
  const [animationResult, setAnimationResult] = useState<'heads' | 'tails' | null>(null)

  const gameStateData = useMemo(() => {
    return accounts.data?.find((account: any) => account.publicKey.equals(gameStatePubkey))
  }, [accounts.data, gameStatePubkey])

  const betAccountQuery = useCoinFlipProgramAccount({
    user: wallet.publicKey || new PublicKey('11111111111111111111111111111111'),
    owner: gameStateData?.account?.owner || new PublicKey('11111111111111111111111111111111'),
    gameNumber: gameStateData?.account?.totalGames?.toNumber()
  })

  const potentialWinnings = useMemo(() => {
    if (!placeBetAmount || !gameStateData?.account) return 0
    const payout = placeBetAmount * 2
    const fee = (payout * gameStateData.account.feePercentage.toNumber()) / 100
    return payout - fee
  }, [placeBetAmount, gameStateData?.account])

  // Reset animation when bet is complete
  useEffect(() => {
    if (betResult === 'won' || betResult === 'lost') {
      setTimeout(() => {
        setShowAnimation(false)
        setBetResult(null)
        setAnimationResult(null)
      }, 8000)
    }
  }, [betResult])

  const checkBetResult = async (gameNumber: number) => {
    if (!wallet.publicKey || !program) return

    const gameNumberBuf = new BN(gameNumber).toArrayLike(Buffer, 'le', 8)
    const [correctBetAccount] = PublicKey.findProgramAddressSync(
      [Buffer.from('bet_account'), wallet.publicKey.toBuffer(), gameNumberBuf],
      (program as any).programId
    )

    let attempts = 0
    const maxAttempts = 10

    const attemptFetch = async () => {
      attempts++
      
      try {
        const betData = await (program.account as any).betAccount.fetch(correctBetAccount)
        
        if (betData?.settled) {
          const coinResult = betData.won 
            ? (betChoice === 0 ? 'heads' : 'tails')
            : (betChoice === 0 ? 'tails' : 'heads')
          
          setAnimationResult(coinResult)
          setBetResult(betData.won ? 'won' : 'lost')
          accounts.refetch()
          return
        }
        
        if (attempts < maxAttempts) {
          setTimeout(attemptFetch, 2000)
        } else {
          setBetResult(null)
          setShowAnimation(false)
        }
      } catch (error) {
        if (attempts < maxAttempts) {
          setTimeout(attemptFetch, 2000)
        } else {
          setBetResult(null)
          setShowAnimation(false)
        }
      }
    }

    setTimeout(attemptFetch, 3000)
  }

  const handlePlaceBet = async () => {
    if (!wallet.publicKey || !gameStateData?.account?.owner || !placeBetAmount || placeBetAmount <= 0) {
      return
    }

    setShowAnimation(true)
    setAnimationResult(null)
    setBetResult('pending')
    
    const amountLamports = placeBetAmount * LAMPORTS_PER_SOL

    try {
      const gameNumberForBet = betAccountQuery.currentGameNumber
      
      await betAccountQuery.placeBet.mutateAsync({
        amount: amountLamports,
        choice: betChoice
      })
      
      checkBetResult(gameNumberForBet)
    } catch (error) {
      console.error("Bet failed:", error)
      setBetResult(null)
      setShowAnimation(false)
    }
  }

  if (!gameStateData?.account) {
    return <div className="text-center py-8 bg-white rounded-2xl shadow-lg">Loading game...</div>
  }

  return (
    <div className="flex-1 flex flex-col items-center justify-center space-y-8">
      {/* Main Game Card */}
      <div className="bg-white/90 backdrop-blur-sm rounded-3xl shadow-2xl border border-white/30 p-8 w-full max-w-2xl">
        {/* Game Header */}
        <div className="text-center mb-8">
          <h1 className="text-5xl font-bold text-gray-900 mb-4">ü™ô Coin Flip</h1>
          <p className="text-xl text-gray-600">Double your SOL or lose it all!</p>
        </div>

        {/* Show animation when active */}
        {showAnimation && (
          <div className="mb-8">
            <CoinFlipAnimation 
              isFlipping={betResult === 'pending' || !!animationResult}
              result={animationResult}
              userBet={betChoice === 0 ? 'heads' : 'tails'}
              onAnimationComplete={() => {}}
            />
          </div>
        )}

        <div className="space-y-8">
          {/* Bet Amount */}
          <div className="text-center">
            <label className="block text-lg font-semibold text-gray-700 mb-4">
              Bet Amount
            </label>
            <div className="flex items-center justify-center gap-4 mb-4">
              <input
                type="number"
                min={0.01}
                step={0.01}
                value={placeBetAmount}
                onChange={(e) => setPlaceBetAmount(Number(e.target.value))}
                disabled={betAccountQuery.placeBet.isPending || showAnimation}
                className="w-32 px-4 py-3 border-2 border-gray-300 rounded-xl text-xl font-bold text-center focus:ring-4 focus:ring-blue-200 focus:border-blue-500 transition-all"
              />
              <span className="text-xl font-bold text-gray-700">SOL</span>
            </div>
            
            {/* Quick Amount Buttons */}
            <div className="flex justify-center gap-3">
              {[0.1, 0.5, 1.0, 2.0].map(amount => (
                <button
                  key={amount}
                  onClick={() => setPlaceBetAmount(amount)}
                  disabled={betAccountQuery.placeBet.isPending || showAnimation}
                  className="px-4 py-2 text-sm font-semibold bg-gray-100 hover:bg-gray-200 disabled:opacity-50 disabled:cursor-not-allowed rounded-lg transition-all"
                >
                  {amount} SOL
                </button>
              ))}
            </div>
          </div>

          {/* Potential Winnings */}
          {placeBetAmount > 0 && !showAnimation && (
            <div className="bg-gradient-to-r from-yellow-50 to-orange-50 border-2 border-yellow-300 rounded-xl p-6 text-center">
              <p className="text-2xl font-bold text-yellow-800 mb-2">
                Potential Winnings: {potentialWinnings.toFixed(4)} SOL
              </p>
              <p className="text-sm text-yellow-700">
                2x your bet minus {gameStateData.account.feePercentage.toString()}% house fee
              </p>
            </div>
          )}

          {/* Choice Selection */}
          <div className="text-center">
            <label className="block text-lg font-semibold text-gray-700 mb-4">
              Choose Your Side
            </label>
            <div className="grid grid-cols-2 gap-6">
              <button
                onClick={() => setBetChoice(0)}
                disabled={betAccountQuery.placeBet.isPending || showAnimation}
                className={`p-8 rounded-2xl border-3 transition-all transform hover:scale-105 disabled:opacity-50 disabled:cursor-not-allowed disabled:hover:scale-100 ${
                  betChoice === 0
                    ? 'border-orange-500 bg-orange-100 text-orange-700 shadow-lg'
                    : 'border-gray-300 hover:border-gray-400 text-gray-700 bg-white'
                }`}
              >
                <div className="text-6xl mb-3">ü™ô</div>
                <div className="text-xl font-bold">Heads</div>
              </button>
              
              <button
                onClick={() => setBetChoice(1)}
                disabled={betAccountQuery.placeBet.isPending || showAnimation}
                className={`p-8 rounded-2xl border-3 transition-all transform hover:scale-105 disabled:opacity-50 disabled:cursor-not-allowed disabled:hover:scale-100 ${
                  betChoice === 1
                    ? 'border-blue-500 bg-blue-100 text-blue-700 shadow-lg'
                    : 'border-gray-300 hover:border-gray-400 text-gray-700 bg-white'
                }`}
              >
                <div className="text-6xl mb-3">üéØ</div>
                <div className="text-xl font-bold">Tails</div>
              </button>
            </div>
          </div>

          {/* Place Bet Button */}
          <Button
            onClick={handlePlaceBet}
            disabled={betAccountQuery.placeBet.isPending || !placeBetAmount || placeBetAmount <= 0 || !wallet.publicKey || showAnimation}
            className="w-full py-6 text-2xl font-bold bg-gradient-to-r from-green-500 to-emerald-500 hover:from-green-600 hover:to-emerald-600 disabled:opacity-50 disabled:cursor-not-allowed rounded-xl shadow-lg transform transition-all hover:scale-105 disabled:hover:scale-100"
          >
            {showAnimation ? 'Flipping...' : 'üé≤ Flip the Coin!'}
          </Button>
        </div>
      </div>

      {/* Game Stats */}
      <div className="bg-white/80 backdrop-blur-sm rounded-xl p-6 shadow-lg border border-white/20 w-full max-w-2xl">
        <h3 className="text-lg font-bold mb-4 flex items-center gap-2 text-gray-800">
          <TrendingUp className="w-5 h-5 text-blue-500" />
          Game Statistics
        </h3>
        <div className="grid grid-cols-4 gap-4 text-center">
          <div>
            <div className="text-2xl font-bold text-gray-900">
              {gameStateData.account.totalGames.toString()}
            </div>
            <div className="text-sm text-gray-600">Total Games</div>
          </div>
          <div>
            <div className="text-2xl font-bold text-green-600">
              {((gameStateData.account.totalAmount.toNumber()) / LAMPORTS_PER_SOL).toFixed(2)}
            </div>
            <div className="text-sm text-gray-600">Vault (SOL)</div>
          </div>
          <div>
            <div className="text-2xl font-bold text-purple-600">
              {gameStateData.account.feePercentage.toString()}%
            </div>
            <div className="text-sm text-gray-600">House Fee</div>
          </div>
          <div>
            <div className="text-2xl font-bold text-orange-600">50%</div>
            <div className="text-sm text-gray-600">Win Rate</div>
          </div>
        </div>
      </div>
    </div>
  )
}

function GameSelector() {
  const { accounts } = useCoinFlipProgram()
  const [selectedGame, setSelectedGame] = useState<PublicKey | null>(null)

  const availableGames = useMemo(() => {
    return accounts.data?.filter((account: any) => 
      account.account.totalAmount.toNumber() > 0
    ) || []
  }, [accounts.data])

  useEffect(() => {
    if (availableGames.length > 0 && !selectedGame) {
      setSelectedGame(availableGames[0].publicKey)
    }
  }, [availableGames, selectedGame])

  if (availableGames.length === 0) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-50 via-purple-50 to-pink-50 flex items-center justify-center p-4">
        <div className="text-center py-12 bg-white/90 backdrop-blur-sm rounded-3xl shadow-2xl max-w-md mx-auto border border-white/30">
          <Coins className="h-20 w-20 text-gray-400 mx-auto mb-6" />
          <h3 className="text-2xl font-bold text-gray-700 mb-4">No Games Available</h3>
          <p className="text-gray-500">No active games found. Contact an admin to start a game.</p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-purple-50 to-pink-50 p-4">
      <div className="max-w-7xl mx-auto h-full">
        <div className="grid grid-cols-12 gap-6 h-full">
          {/* Left Sidebar */}
          <div className="col-span-12 lg:col-span-3 space-y-6">
            <TopWinnersPanel />
          </div>

          {/* Main Game Area */}
          <div className="col-span-12 lg:col-span-6 flex flex-col">
            {selectedGame && (
              <BettingInterface gameStatePubkey={selectedGame} />
            )}
          </div>

          {/* Right Sidebar */}
          <div className="col-span-12 lg:col-span-3 space-y-6">
            <RecentActivityPanel />
          </div>
        </div>
      </div>
    </div>
  )
}

export default function CoinFlipGame() {
  const wallet = useWallet()

  if (!wallet.publicKey) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-50 via-purple-50 to-pink-50 flex items-center justify-center p-4">
        <div className="text-center max-w-md mx-auto p-12 bg-white/90 backdrop-blur-sm rounded-3xl shadow-2xl border border-white/30">
          <Coins className="h-24 w-24 text-gray-400 mx-auto mb-8" />
          <h1 className="text-4xl font-bold text-gray-900 mb-6">Coin Flip Game</h1>
          <p className="text-xl text-gray-600 mb-8">Connect your wallet to start playing!</p>
        </div>
      </div>
    )
  }

  return <GameSelector />
}